.exp-container {
  /* border: 1px solid blue; */
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  color: gray;
  border-bottom: 1px solid black;
}

h2 {
  color: black;
  font-size: 3.5rem;
}

.exp-item-container {
  margin-bottom: 2rem;
  font-size: 1.2rem;
}

.up-item-container {
  display: block;
  margin-bottom: 1.5rem;
  color: black;
}
.upper-section {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  font-size: 1.6rem;
}

.period {
  font-size: 1rem;
  color: rgb(248, 186, 71);
}

.cooperations {
  font-size: 1.2rem;
  color: gray;
}

.exp-descripe {
  font-size: 1rem ;
}

/* --------------responsive--------------- */

@media only screen and (max-width: 610px) {
  .exp-container {
    height: fit-content;
    padding: 10rem 0;
  }

  h2 {
    font-size: 2rem;
  }

  .position {
    font-size: 1.2rem;
  }

  .period {
    font-size: 1.2rem;
  }

  .cooperations {
    font-size: 1rem;
  }

  .upper-section {
    flex-direction: column;
    height: 3rem;
  }
}